const axios = require("axios");
var bcrypt = require("bcryptjs");

export async function getAllUsers() {
	try {
		const response = await axios.get(
			"https://0k0kfx0my5.execute-api.us-east-2.amazonaws.com/get-users"
		);
		//console.log("response  ", response);
		return response.data;
	} catch (error) {
		return [];
	}
}

export async function getUser(id) {
	try {
		const response = await axios.get(
			"https://8vzn07fksg.execute-api.us-east-2.amazonaws.com/get-user?id=" + id
		);
		//console.log("response  ", response);
		return response.data;
	} catch (error) {
		return [];
	}
}

var salt = bcrypt.genSaltSync(10);

export async function tryLogin(id, password) {
	try {
		const response = await axios.get(
			"https://75ivbtudj5.execute-api.us-east-2.amazonaws.com/get-hashed-password?id=" +
				id
		);
		//console.log("response  ", response);
		return bcrypt.compareSync(password, response.data[0].hashedPassword);
	} catch (error) {
		return false;
	}
}

export async function encryptString(string) {
	return bcrypt.hashSync(string, salt);
}

export async function compareToHashed(string, hashed) {
	return bcrypt.compareSync(string, hashed);
}

export async function createUser(id, name, password, email) {
	try {
		const options = {
			method: "POST",
			url: "https://zo7h26aamk.execute-api.us-east-2.amazonaws.com/add-user",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
			},
			data: {
				name,
				id,
				email,
				hashedPassword: bcrypt.hashSync(password, salt),
			},
		};

		const res = await axios.request(options);
		console.log("response  ", res);
		return res.data;
	} catch (error) {
		return [];
	} // https://avatars.dicebear.com/api/croodles-neutral/cregg.svg
}
