import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Alert from "react-bootstrap/Alert";
import Card from "../components/Card";
import { createUser, encryptString } from "../services/usersService";
import { Link } from "react-router-dom";
import { v4 as uuidv4 } from "uuid";
import { useCookies } from "react-cookie";

export default function UserCreate() {
	const [errorMSG, setErrorMSG] = useState("");
	const [error, setError] = useState(false);
	const [emailValue, setEmailValue] = useState("");
	const [userValue, setUserValue] = useState("");
	const [passValue, setPassValue] = useState("");
	const [passCheckValue, setPassCheckValue] = useState("");

	const [userCreated, setUserCreated] = useState(false);
	const [userId, setUserId] = useState("");

	const [savedHashedPass, setSavedHashedPass, removeSavedHashedPass] =
		useCookies(["hashed-password"]);

	return (
		<div
			style={{
				display: "flex",
				justifyContent: "center",
				alignItems: "center",
			}}
		>
			<Card>
				{userCreated ? (
					<>
						<Link to={"/user/" + userId}>Visit Your New Profile!</Link>
					</>
				) : (
					<>
						{error ? <Alert variant="danger">{errorMSG}</Alert> : ""}
						<Form
							onSubmit={(e) => {
								e.preventDefault();
								console.log("hi");
								if (passValue != passCheckValue) {
									setError(true);
									setErrorMSG("Your Passwords Don't Match");
								} else {
									let id = uuidv4();
									createUser(id, userValue, passValue, emailValue);
									setUserId(id);
									setUserCreated(true);
									setSavedHashedPass(encryptString(passValue));
								}
							}}
						>
							<Form.Group className="mb-3" controlId="formUsername">
								<Form.Label>Username</Form.Label>
								<Form.Control
									onChange={(e) => {
										setUserValue(e.target.value);
										setErrorMSG("");
										setError(false);
									}}
									value={userValue}
									required
									type="text"
									placeholder="Enter username"
									minLength="4"
									maxLength="12"
									//pattern="[A-Za-z0-9_]"
								/>
								<Form.Text className="text-muted">
									No spaces or special characters.
								</Form.Text>
							</Form.Group>
							<Form.Group className="mb-3" controlId="formEmail">
								<Form.Label>Email address</Form.Label>
								<Form.Control
									onChange={(e) => {
										setEmailValue(e.target.value);
										setErrorMSG("");
										setError(false);
									}}
									required
									value={emailValue}
									type="email"
									placeholder="Enter email"
								/>
								<Form.Text className="text-muted">
									We'll never share your email with anyone else. This is used
									exclusively for account recovery.
								</Form.Text>
							</Form.Group>

							<Form.Group className="mb-3" controlId="formPassword">
								<Form.Label>Password</Form.Label>
								<Form.Control
									onChange={(e) => {
										setPassValue(e.target.value);
										setErrorMSG("");
										setError(false);
									}}
									required
									value={passValue}
									type="password"
									placeholder="Password"
									pattern="^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*_=+-]).{8,12}$"
								/>
							</Form.Group>
							<Form.Group className="mb-3" controlId="formPasswordCheck">
								<Form.Control
									onChange={(e) => {
										setPassCheckValue(e.target.value);
										setErrorMSG("");
										setError(false);
									}}
									required
									value={passCheckValue}
									type="password"
									placeholder="Confirm Password"
									pattern="^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*_=+-]).{8,12}$"
								/>
								<Form.Text className="text-muted">
									<ul>
										<li>Between 8 and 12 characters</li>
										<li>1 number and 1 symbol</li>
										<li>At least 1 uppercase and 1 lowercase letter</li>
									</ul>
								</Form.Text>
							</Form.Group>

							<Button variant="primary" type="submit">
								Submit
							</Button>
						</Form>
					</>
				)}
			</Card>
		</div>
	);
}
